import {Component} from '@angular/core';
import {Router} from '@angular/router-deprecated';
import {FORM_DIRECTIVES} from '@angular/common';
import {User,UtilService} from "../../shared/index";
import {ModifyPasswordComponent} from "../../+modifypassword/components/modifypwd.component";
import {Response} from '@angular/http';
/**
 * Created by Ping on 2016/5/10.
 */
@Component({
  selector: 'forgot-box',
  providers: [UtilService],
  directives:[FORM_DIRECTIVES,ModifyPasswordComponent],
  template: require('app/+forgot/components/forgot.component.html')
})

export class ForgotComponent {
  mobile:string = ""; //
  isDisabled:boolean = false;
  user:User;
  isNext:boolean = false; //
  message:string; //
  errorMsg:string;//
  error:boolean; //
  vdcode:string; //
  isRegistered:boolean = false; //

  constructor(private router:Router, private _util:UtilService) {
    this.user = new User();
    this.message = '';
    this.error = false;
  }

  //
  toLogin() {
    this.router.parent.navigate(['Login']);
  }

  //
  countBack() {
    var i = 60;
    var intervalid:any;
    intervalid = setInterval(()=> {
      i--;
      if (i >= 1) {
        this.isDisabled = true; //
        this.message = i + '';
      } else {
        this.isDisabled = false; //
        this.message = '';
        clearInterval(intervalid);
      }
    }, 1000);
  }

  //
  getCode() {


    if (this.mobile.contains("@")) { //
      alert('');
      this.user.email = this.mobile;
      this._util.getValidMdPassword(JSON.stringify(this.user)).subscribe((res:Response)=> {
        var data = res.json();
        if (data.status === "0") {
          this.errorMsg = '';
          this.error = true;
          this.isRegistered = false;
          setTimeout(() => {
            this.error = false;
          }, 8000);
        } else if (data.status === "1") {
          this.vdcode = data.result;
          this.isRegistered = true;
        }
      });
      this.countBack();
    } else if (this.mobile.length == 11) {
      let value = this.mobile
      if (parseInt(value) > 10000000000) {
        //
        alert('');
        this.user.phone = this.mobile;
        this._util.getValidMdPassword(JSON.stringify(this.user)).subscribe((res:Response)=> {
          var data = res.json();
          if (data.status === "0") {
            this.errorMsg = '';
            this.error = true;
            this.isRegistered = false;
            setTimeout(() => {
              this.error = false;
            }, 8000);
          } else if (data.status === "1") {
            this.vdcode = data.result;
            this.isRegistered = true;
          }
        });
        this.countBack();
      } else {
        alert("")
      }

    }
  }

  //
  recallPwd(form:any) {
    if (!this.isRegistered) {
      this.errorMsg = '';
      this.error = true;
      setTimeout(() => {
        this.error = false;
      }, 3000);
    } else if (form.validCode !== this.vdcode) {
      this.errorMsg = '';
      this.error = true;
      setTimeout(() => {
        this.error = false;
      }, 3000);
    } else { //
      this.user.phone = form.phone;
      this.isNext = true;
    }
  }
}



