  //      clearInterval(intervalid);
  //    }
  //  }, 1000);
  //}

  ////
  //  getCode() {
  //    if(this.mobile){ //
  //      alert('');
  //      this._util.getValidCode(this.mobile).subscribe((res:Response)=>{
  //        var data = res.json();
  //        if(data.status ==="0"){
  //          this.errorMsg = '';
  //          this.error = true;
  //          setTimeout(() => {
  //            this.error = false;
  //          }, 10000);
  //          this.isRegistered = true;
  //        }else if(data.status ==="1"){
  //          this.vdcode = data.result;
  //        }
  //      });
  //      this.countBack();
  //    }else{
